.movies-list {
  padding: 10px 100px;
  text-align: start;
}

.movies-item {
  margin-bottom: 5px;

  &:last-child {
    margin-bottom: 0px;
  }
}

.movies-link {
  color: black;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;

  &:hover,
  &:focus {
    color: #ff6b01;
  }
}
